@mixin r($wt:100%,$ht:auto){
  position:relative;
  width:$wt;
  height: $ht;  
  float: left;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: 0;
  @content;
}
@mixin am($wt:100%,$ht:100%,$left:50%,$top:50%){
  position:absolute;
  width:$wt;
  height: $ht;  
  top: $top;
  left:$left;
  transform:translate(-50%,-50%);
  @content;
}
