@mixin font($fs:15px,$lh:27px,$co:#FFF,$fw:normal,$ff:"微軟正黑體"){
  font-size: $fs;
  line-height: $lh;
  font-weight: $fw;
  font-family: Arial,$ff;
  color: $co;
  @content;
}
@mixin fontvw($fs:15,$lh:27,$co:#FFF,$fw:normal,$ff:"微軟正黑體"){
  font-size: getVW($fs);
  line-height: getVW($lh);
  font-weight: $fw;
  font-family: Arial,$ff;
  color: $co;
  @content;
}
